import React from 'react';
import { observer } from 'mobx-react';

const Footer = observer(() => {
    const images = [
        { url: "img/HTML5-logo.png" },
        { url: "img/CSS3-logo.png" },
        { url: "img/JavaScript-logo.png" },
        { url: "img/React-logo.png" },
        { url: "img/Redux-logo.png" },
        { url: "img/Mobex-logo.png" },
        { url: "img/Nodejs-logo.png" },
        { url: "img/MySql-logo.png" },
    ];

    return (
        <div className="footer">
            <div className='inConts'>
                <div className='tac'>
                    {images.map((item, idx) => {
                        return <div className='imgArea' key={idx}><img src={item.url} /></div>
                    })}
                </div>
                <div className='text'>
                    <p>
                        <span>   () : My Page 2023 (Pro. EunYul)</span>
                        <span>  : EunYul</span>
                        <span> git address : https://github.com/sonjaesung/</span>
                        <span>  :  </span>
                    </p>
                    <p className='innerText'>
                        <span>  :<strong>010-2689-2631</strong></span>
                        <span>  : <strong>loki92@naver.com</strong></span>
                    </p>
                </div>
            </div>
        </div>
    );
});

export default Footer;
